import { neon, NeonQueryFunction } from "@neondatabase/serverless";
// Types
import { parsedProducts } from "./../types/product";

class ProductService {
  sql: NeonQueryFunction<false, false>;

  constructor() {
    this.sql = neon(
      "postgresql://inventory_db_owner:bZYGkrhIN94E@ep-muddy-darkness-a17hqwod.ap-southeast-1.aws.neon.tech/inventory_db?sslmode=require"
    );
  }

  async bulkInsert(data: parsedProducts[]): Promise<boolean> {
    const query = `
    INSERT INTO products (product_id, title, tags, created_at, updated_at, sku) 
    VALUES ($1, $2, $3, $4, $5, $6)
  `;
    try {
      for (const product of data) {
        await this.sql(query, [
          product.id,
          product.title,
          product.tags,
          product.created_at,
          product.updated_at,
          product.sku,
        ]);
      }

      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  }

  async products() {
    try {
      const products = await this.sql("SELECT * FROM products");
      return {
        data: products,
        success: true,
      };
    } catch (error) {
      return {
        data: null,
        success: false,
      };
    }
  }

  async product(productId: number) {
    try {
      const query = "SELECT * FROM products WHERE id = $1";
      return await this.sql(query, [productId]);
    } catch (error) {
      return null;
    }
  }

  async updateAllProducts() {
    const products = await this.products();
    const query = `UPDATE products SET title = $1, updated_at = $2 WHERE id = $3`;
    try {
      if (!products.data) {
        throw new Error("There are no products in the database");
      }
      for (const product of products.data) {
        await this.sql(query, [
          `${product.title} ${product.sku}`,
          new Date(),
          product.id,
        ]);
      }
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  }

  async deleteProduct(productId: number) {
    const product = await this.product(productId);
    const query = `DELETE products WHERE id = $1`;
    //   title + sku
    try {
      if (!product) {
        throw new Error("Product ID not found");
      }
      await this.sql(query, [productId]);
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  }
}

export default ProductService;
